.liquid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: #a4acc4;
  overflow: hidden;
}
.liquid::before,
.liquid::after {
  content: "";
  position: absolute;
  width: 280px;
  height: 280px;
  border-radius: 50px;
  transition: all 0.8s;
}
.liquid::before {
  top: -275px;
  left: -50%;
  background: rgb(45, 45, 45);
  -webkit-animation: anim 5s linear infinite;
          animation: anim 5s linear infinite;
}
.liquid::after {
  top: -270px;
  left: -50%;
  background: rgba(45, 45, 45, 0.666);
  -webkit-animation: anim 5s linear infinite;
          animation: anim 5s linear infinite;
}
@-webkit-keyframes anim {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes anim {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.main-info-button .main-info-al:hover .liquid::before,
.main-info-button .main-info-al:hover .liquid::after {
  top: -350px;
}

